<template>

        <div>
          <el-col :span="17"><h1 style="margin-left: 50%">To do List</h1>
          </el-col>
           <el-col :span="3"><el-button @click="showDialog = true">add</el-button></el-col>
          <new-todo-list v-if="showDialog" @close = "showDialog = false"></new-todo-list>

            <el-table :align = "alignClass" :data="todoList" style="width: 66%; margin-left: 25%">
                <el-table-column type="expand">
                    <template scope="props">
                        <p>Title: {{ props.row.title }}</p>
                        <p>Priority: {{ props.row.priorityId }}</p>
                        <p>Due Date: {{ props.row.dueDate }}</p>
                        <p>Create Date: {{ props.row.createDate }}</p>
                        <p>Description: {{ props.row.text }}</p>
                        <p>Done: <i v-bind:class="{ 'el-icon-circle-check' : props.row.done}"></i></p>
                    </template>
                </el-table-column>
                <el-table-column
                        label="Title"
                        prop="title">
                </el-table-column>
                <el-table-column
                        label="Due Date"
                        prop="dueDate"
                        sortable>
                </el-table-column>
                <el-table-column
                        label="Priority"
                        prop="priorityId"
                        sortable>
                </el-table-column>
                <el-table-column
                        label="Done"
                        prop="done">
                    <template scope="props">
                        <i v-bind:class="{'el-icon-circle-check' : props.row.done}"></i>
                        <i v-bind:class="{'el-icon-check' : !props.row.done}" @click="handleDone(props.$index, props.row)"></i>
                    </template>

                </el-table-column>
                <el-table-column
                        label="Operations">
                    <template scope="props">
                        <router-link to="/">
                          <i class="el-icon-edit" size="small"
                                                @click="handleEdit(props.$index, props.row)" style="margin-right: 10px"></i>
                        </router-link>

                        <router-link to="">
                            <i class="el-icon-delete"
                               size="small"
                               type="danger"
                               @click="handleDelete(props.$index, scope.row)"></i>
                        </router-link>
                    </template>
                </el-table-column>
            </el-table>

        </div>


</template>
<script>
   // import todoList from '../../../data/todoList'
   import Vue from 'vue'
    import {todoListUrl,getHeader} from '../../../env'
   import ElButton from "../../../../node_modules/element-ui/packages/button/src/button.vue";
   import ElRow from "element-ui/packages/row/src/row";
   import ElCol from "element-ui/packages/col/src/col";
   import NewTodoList from './NewTodoList.vue'
    export default {
      components: {
        ElCol,
        ElRow,
        ElButton,
        NewTodoList
      },
      data() {
//            const finish = info;
            return {
                todoList:null,
                heightClass: "auto",
                alignClass: "center",

              showDialog: false
            }
        },
        created () {
            this.fetchData()
        },
//        computed:{
//            todoList
//        },
        methods: {
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            },
            handleDone(index, row) {
                todoList[index].done = true;
                console.log(index,row);

            },

            fetchData(){
//                this.todoList = null;
                this.$http.get('http://localhost:8080/todo/',getHeader()).then(response=> {
                        if (response.status == 200) {
                            this.todoList = response.data
                        }
                    })
            }

        }
    }
</script>
<style scoped>
    /*.el-table .complete {*/
        /*background: #c9e5f5;*/
    /*}*/


</style>



